import React, { Component } from 'react';
import { Logger } from '../../Utils/Utils';
import { Badge, Button, Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import { requestHeaderPOST } from "../../Rest-API/Header";
import Loading from "../../Loading";
//import { Link } from 'react-router-dom';

const FILE_NAME = "File.js";
const API_URL = "http://192.168.1.109/nexus/dirinfo";
// const API_URL="http://localhost:7979/dirinfo";
class File extends Component {
    constructor(props) {
        super(props);

        this.state = {
            currentDir: "/usr/lib/tomcat/apache-tomcat-9.0.37/webapps/tv_shows",
            currentDirData: {},
            isLoaded: false
        };
        this.onDirInputChange = this.onDirInputChange.bind(this);
    }

    componentDidMount() {
        fetch(API_URL, requestHeaderPOST(this.state.currentDir))
            .then(res => res.json())
            .then(
                (result) => {
                    Logger(FILE_NAME, "componentDidMount", "FETCH_1", "result", result);
                    this.setState({
                        currentDirData: result.data,
                        isLoaded: true
                    })
                },
                (error) => {
                    Logger(FILE_NAME, "componentDidMount", "FETCH_1", "error", error);
                }
            );
    }

    onDirInputChange(event) {
        const changedDir = event.target.value;
        Logger(FILE_NAME, "callApi", "", "changedDir", changedDir);

        this.setState({
            currentDir: changedDir
        });

    }

    callApi = (requestedDir) => {
        this.setState({
            currentDir: requestedDir,
            currentDirData: {},
            isLoaded: false
        })

        fetch(API_URL, requestHeaderPOST(requestedDir))
            .then(res => res.json())
            .then(
                (result) => {
                    Logger(FILE_NAME, "callApi", "FETCH_1", "result", result);
                    this.setState({
                        currentDirData: result.data,
                        isLoaded: true
                    })
                },
                (error) => {
                    Logger(FILE_NAME, "callApi", "FETCH_1", "error", error);
                }
            );
    }
    render() {
        Logger(FILE_NAME, "render", "", "this", this);

        return (
            <div className="animated fadeIn">
                <Row>
                    <Col lg="12">
                        <Card style={{ fontSize: '28px', fontFamily: "Courier New" }}>
                            <CardHeader className="bg-primary"><h1>File Iterator</h1></CardHeader>
                            <CardBody>
                                Current Dir. :<p>{this.state.currentDir}</p>
                                <input type="text" id="requestedDir" size={50} value={this.state.currentDir} onChange={this.onDirInputChange} required={true} />
                                &nbsp;&nbsp;
                                    <Button active color="success" onClick={() => {
                                    this.callApi(this.state.currentDir)
                                }}>Call backend</Button>
                                <br /><br />
                                <Button color="danger" onClick={() => {
                                    const currentDir = this.state.currentDir;
                                    var res = currentDir.split("/");
                                    var output = "";

                                    res.forEach(
                                        (e, index) => {
                                            if (index !== 0 && index < res.length - 1)
                                                output += ("/" + e);
                                        }
                                    )
                                    this.callApi(output);
                                }}>Go up a dir.
                                <i className="cui-arrow-top icons font-2xl d-block "></i>
                                </Button>
                                <br /><br />
                                {
                                    (this.state.isLoaded) ? (
                                        <Col lg="12">
                                            <Row>
                                                <Col>

                                                    <h1><Badge color="primary">Folders </Badge></h1>
                                                    {
                                                        this.state.currentDirData.directories.map((dir, index) =>
                                                            <div
                                                                key={"f" + index}
                                                                onClick={() => {
                                                                    this.callApi(dir.fullPath)
                                                                }}
                                                                style={{
                                                                    borderColor: "#2196F3",
                                                                    backgroundColor: "#ddffff",
                                                                    borderLeft: "6px solid",
                                                                    padding: "10px",
                                                                    margin: "5px"
                                                                }}>
                                                                <i className="fa fa-folder" aria-hidden="true"></i> : {dir.name}
                                                                <button><i className="cui-action-redo"></i>
                                                                </button>
                                                            </div>
                                                        )
                                                    }
                                                </Col>
                                                <br />
                                                <Col>
                                                    <h1><Badge color="success">Files </Badge></h1>
                                                    {
                                                        this.state.currentDirData.files.map((file, index) =>
                                                            <div key={"g" + index}
                                                                onClick={() => {
                                                                    window.open(file.relativePath, "_blank");
                                                                }}
                                                                style={{
                                                                    borderColor: "#ffb3b3",
                                                                    backgroundColor: "#ffb3b3",
                                                                    borderLeft: "6px solid",
                                                                    padding: "10px",
                                                                    margin: "5px"
                                                                }}><i className="fa fa-file" aria-hidden="true"></i> : {file.name}

                                                                {(file.relativePath.length > 0) && <Button color="" ><i className="cui-graph icons font-2xl d-block mt-4"></i></Button>}
                                                            </div>
                                                        )
                                                    }
                                                </Col>
                                            </Row>
                                        </Col>
                                    )
                                        : <Loading />
                                }

                            </CardBody>
                        </Card>
                    </Col>
                </Row>


            </div>)
    }
}

export default File;
